import { ChangeDetectionStrategy, Component, ElementRef, EventEmitter, Input, OnChanges, Output, SimpleChanges, ViewChild, forwardRef } from '@angular/core';
import { CommonModule } from '@angular/common';
import { NG_VALUE_ACCESSOR, NG_VALIDATORS, AbstractControl, FormsModule, ValidationErrors, ControlValueAccessor, Validator } from '@angular/forms';
import { Tag } from 'src/app/models/trip.model';

@Component({
  selector: 'app-tags',
  standalone: true,
  imports: [CommonModule, FormsModule],
  templateUrl: './tags.component.html',
  styleUrls: ['./tags.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi:true,
      useExisting: forwardRef(() => TagsComponent) 
    },
    {
      provide: NG_VALIDATORS,
      multi: true,
      useExisting: forwardRef(() => TagsComponent) 
    }
  ],
  changeDetection: ChangeDetectionStrategy.OnPush
})

export class TagsComponent implements ControlValueAccessor, Validator, OnChanges  {
  @Input() caption: string;
  @Output() onTagClick = new EventEmitter<Tag>();
  @Input() tags!: Tag[];

  ngOnChanges(changes: SimpleChanges) {
    let tagChanges = changes['tags'];
    if (tagChanges && tagChanges.previousValue && tagChanges.currentValue && tagChanges.previousValue !== tagChanges.currentValue) {
      this.tags = tagChanges.currentValue;
    }
  }

  openDetails(tag?: Tag) {
    this.onTagClick.emit(tag);
  }

  touched = false;
  disabled = false;

  writeValue(inputTags: Tag[]): void {
    this.tags = inputTags ?? [];
  }

  registerOnChange(onChangeFn: any): void {
    this.onChange = onChangeFn;
  }

  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }

  validate(control: AbstractControl<any, any>): ValidationErrors | null {
    //return (!this.allowEmptyTags && this.tags?.length === 0) ? { tagsAreEmpty: true } : null;
    return null;
  }

  registerOnValidatorChange?(fn: () => void): void {
    //throw new Error('Method not implemented.');
  }

  onChange = (tags) => {};

  registerOnTouched(onTouchedFn: any): void {
    this.onTouched = onTouchedFn;
  }

  onTouched = () => {};

  setDisabledState?(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }
}