import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { CommonModule } from '@angular/common';
import { OffcanvasbagComponent } from '../offcanvasbag/offcanvasbag.component';
import { Trip } from 'src/app/models/trip.model';
import { CheckList } from 'src/app/models/checklist.model';
import { PrepareList } from 'src/app/models/preparelist.const';
import { Store } from '@ngrx/store';
import { TripStoreState } from 'src/app/models/tripStoreState';
import { componentBase } from '../componentBase';
import * as tripActions from 'src/app/store/trip.action';
import { FormsModule } from '@angular/forms';

@Component({
  selector: 'app-prepare',
  standalone: true,
  imports: [CommonModule, FormsModule, OffcanvasbagComponent],
  templateUrl: './prepare.component.html',
  styleUrls: ['./prepare.component.scss']
})
export class PrepareComponent extends componentBase implements OnInit {
  @Input() trip!: Trip;
  @ViewChild(OffcanvasbagComponent)
  private offcanvasbagComponent: OffcanvasbagComponent;
  prepareList: CheckList[];
  isChanged: boolean = false;
  preparecheckInput: string = '';

  constructor(store: Store<TripStoreState>) {
    super(store);
    this.prepareList = PrepareList;
  }
  
  openBagCheckList() {
    this.offcanvasbagComponent.toggleOffcanvas();
  }

  ngOnInit(): void {
    if (this.trip && this.trip.prepareCheck) {
      this.prepareList = this.trip.prepareCheck;
    }
  }

  // mergePrepareList(firstArray: CheckList[], secondArray: CheckList[]): CheckList[] {
  //   return firstArray.map((firstObj) => {
  //     const matchingObj = secondArray.find(
  //       (secondObj) => secondObj.name === firstObj.name
  //     );
  
  //     if (matchingObj) {
  //       return { ...firstObj, ...matchingObj };
  //     }
  
  //     return firstObj;
  //   });
  // }

  get sortedPrepareCheckItems() {
    return [...this.prepareList].sort((a, b) => {
      if (a.checked && !b.checked) {
        return 1;
      } else if (!a.checked && b.checked) {
        return -1;
      } else {
        return 0;
      }
    });
  }

  addtoCheckList() {
    if (this.preparecheckInput.length > 0 && !this.prepareList.find(item => item.name === this.preparecheckInput)) {
        this.prepareList = [...this.prepareList, { name: this.preparecheckInput, checked: false }];
    }
    this.preparecheckInput = "";
    this.markasChanged();
  }

  removefromCheckList(value: string) {
    this.prepareList = this.prepareList.filter(s => s.name != value);
    this.markasChanged();
  }

  onCheckedChange(item, event) {
    const updatedItem = { ...item, checked: event.target.checked };
    const index = this.prepareList.indexOf(item);
    this.prepareList = [...this.prepareList.slice(0, index), updatedItem, ...this.prepareList.slice(index + 1)];
    this.markasChanged();
  }

  markasChanged() {
    if (!this.isChanged) {
      this.isChanged = true;
    }
  }

  save() {
    this.store.dispatch(tripActions.updatePrepareList({ prepareCheck : this.prepareList }));
    this.isChanged = false;
  }

  cancelChanges() {
    this.prepareList = this.trip.prepareCheck ?? PrepareList;
    this.isChanged = false;
  }
  
}
