import { ChangeDetectionStrategy, Component, Input, OnInit } from '@angular/core';
import { CommonModule } from '@angular/common';

@Component({
  selector: 'app-tripitem',
  standalone: true,
  imports: [CommonModule],
  templateUrl: './tripitem.component.html',
  styleUrls: ['./tripitem.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class TripitemComponent implements OnInit  {
  @Input() trip;
  @Input() tagRequired: boolean = true;
  title: string;
  tagText!: string;
  tagStyle: string

  ngOnInit() {
    if (this.trip) {
      this.title = this.trip.places.map(s => s.name).join(", ");
      if (this.tagRequired) {
        if (new Date(this.trip.start_at) >= new Date()) {
          this.tagText = 'Upcoming';
          this.tagStyle = 'upcoming';
        } else {
          this.tagText = 'In Progress';
          this.tagStyle = 'inprogress';
        }
      }
    }
  }
}
