import { Component, OnDestroy, OnInit } from '@angular/core';
import { CommonModule } from '@angular/common';
import { componentBase } from '../../componentBase';
import { Store } from '@ngrx/store';
import { TripStoreState } from 'src/app/models/tripStoreState';
import { FormBuilder, FormControl, FormGroup, ReactiveFormsModule, Validators } from '@angular/forms';
import * as tripActions from 'src/app/store/trip.action';
import { EventTypes } from 'src/app/models/event-types.enum';
import { Subscription } from 'rxjs';
import { ToastService } from 'src/app/services/toast.service';

@Component({
  selector: 'app-feedback',
  standalone: true,
  imports: [CommonModule, ReactiveFormsModule],
  templateUrl: './feedback.component.html',
  styleUrls: ['./feedback.component.scss']
})
export class FeedbackComponent extends componentBase implements OnInit, OnDestroy {
  reactiveForm: FormGroup;
  formValid: boolean = false;
  formDirty: boolean = false;
  feedbackType: EventTypes;
  formSubscription: Subscription;

  constructor(store: Store<TripStoreState>, private fb: FormBuilder, private toastService: ToastService) {
    super(store);
    this.reactiveForm = this.fb.group({
      message: new FormControl<string>(null, Validators.required),
    });
  }
  ngOnInit(): void {
    this.formSubscription = this.reactiveForm.valueChanges.subscribe(data => {
      this.setFormState();
    })
  }

  setFormState() {
    this.formValid = this.reactiveForm.valid;
    this.formDirty = this.reactiveForm.dirty;
  }

  save() {
    if (this.formValid && this.formDirty) {
      const { message } = this.reactiveForm.value;
      this.store.dispatch(tripActions.postFeedback({ message }));
      this.toastService.showWarningToast("Feedback submitted", "Thanks for being a feedback superhero. We appreciate you!");
      this.reactiveForm.markAsPristine();
      this.reset();
    }
  }

  reset() {
    this.reactiveForm.reset({
      comments: null
    });
    this.setFormState();
  }

  ngOnDestroy(): void {
    if (this.formSubscription) {
      this.formSubscription.unsubscribe();
    }
  }
}
