import { ChangeDetectionStrategy, Component, ElementRef, EventEmitter, Input, OnChanges, OnDestroy, OnInit, Output, SimpleChanges, ViewChild } from '@angular/core';
import { CommonModule } from '@angular/common';
import { NgbCalendar, NgbDate, NgbDatepicker, NgbDatepickerModule, NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { DropdownOption } from 'src/app/models/dropdownOption';
import { Activity, Place, Stay, Transport, Trip, Tag, Food, Shopping, Notes } from 'src/app/models/trip.model';
import { FormBuilder, FormControl, FormGroup, ReactiveFormsModule } from '@angular/forms';
import { Store } from '@ngrx/store';
import { TripStoreState } from 'src/app/models/tripStoreState';
import { componentBase } from '../componentBase';
import * as tripActions from 'src/app/store/trip.action';
import { offcanvas } from 'src/app/models/offcanvas.model';
import * as moment from 'moment';
import { TagsComponent } from '../common/tags/tags.component';
import { OffcanvastagComponent } from '../offcanvastag/offcanvastag.component';
import { Placeholders } from 'src/app/models/placeholder.enum';
import { nanoid } from 'nanoid';

@Component({
  selector: 'app-place',
  standalone: true,
  imports: [CommonModule, ReactiveFormsModule, NgbDatepickerModule, TagsComponent, OffcanvastagComponent],
  templateUrl: './place.component.html',
  styleUrls: ['./place.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class PlaceComponent extends componentBase implements OnInit, OnChanges, OnDestroy {
  @Input() slideIndex!: number;
  reactiveForm: FormGroup;
  @ViewChild(NgbDatepicker, { static: false, read: ElementRef }) datePicker: ElementRef<NgbDatepicker>;
  @ViewChild(OffcanvastagComponent)
  private offcanvastagComponent: OffcanvastagComponent;
  @Input() trip!: Trip;
  @Output() onNavigate = new EventEmitter<boolean>();
  formValid: boolean = false;
  formDirty: boolean = false;
  nextSlideExists: boolean = false;
  offCanvasData: offcanvas = {};
  isPastTrip: boolean = false;
  placeholders = Placeholders;

  constructor(private fb: FormBuilder, calendar: NgbCalendar, store: Store<TripStoreState>) {
    super(store);
  }

  ngOnInit(): void {
    if (this.trip) {
      this.setupReactiveForm();
    }
  }

  setupReactiveForm(isReset: Boolean = false) {
    let currentPlace = this.trip.places[this.slideIndex];
    currentPlace = {...currentPlace, transports: currentPlace.transports ?? [], stays: currentPlace.stays ?? [],
      activities: currentPlace.activities ?? [], food: currentPlace.food ?? [], shopping : currentPlace.shopping ?? [], 
      notes: currentPlace.notes ?? []
    }
    this.nextSlideExists = !!this.trip.places[this.slideIndex + 1];

    let tripStartAt = new Date(this.trip.start_at);
    let tripEndAt = new Date(this.trip.end_at);
    if (tripEndAt < new Date()) {
      this.isPastTrip = true;
    }
    this.minDate = new NgbDate(tripStartAt.getFullYear(), tripStartAt.getMonth() + 1, tripStartAt.getDate());
    this.maxDate = new NgbDate(tripEndAt.getFullYear(), tripEndAt.getMonth() + 1, tripEndAt.getDate());
    
    const { name, start_at, end_at } = currentPlace;
    let startAt = start_at ? new Date(start_at) : null;
    let endAt = end_at ? new Date(end_at) : null;

    if (isReset) {
      this.reactiveForm.reset({
        name: name,
        fromDate: startAt ? new NgbDate(startAt.getFullYear(), startAt.getMonth() + 1, startAt.getDate()) : null,
        toDate: endAt ? new NgbDate(endAt.getFullYear(), endAt.getMonth() + 1, endAt.getDate()) : null,
        transports: currentPlace.transports,
        stays: currentPlace.stays,
        activities: currentPlace.activities,
        food: currentPlace.food,
        shopping: currentPlace.shopping,
        notes: currentPlace.notes
      });
    } else {
      this.reactiveForm = this.fb.group({
        name: new FormControl<string>(name),
        fromDate: new FormControl<NgbDate | null>(startAt ? new NgbDate(startAt.getFullYear(), startAt.getMonth() + 1, startAt.getDate()) : null),
        toDate: new FormControl<NgbDate | null>(endAt ? new NgbDate(endAt.getFullYear(), endAt.getMonth() + 1, endAt.getDate()) : null),
        transports: new FormControl<Transport[]>(currentPlace.transports),
        stays: new FormControl<Stay[]>(currentPlace.stays),
        activities: new FormControl<Activity[]>(currentPlace.activities),
        food: new FormControl<Food[]>(currentPlace.food),
        shopping: new FormControl<Shopping[]>(currentPlace.shopping),
        notes: new FormControl<Notes[]>(currentPlace.notes)
      });
    }
  }

  setFormState() {
    this.formValid = this.reactiveForm.valid;
    this.formDirty = this.reactiveForm.dirty;
  }

  ngOnChanges(changes: SimpleChanges) {
    let tripIdChanges = changes['trip'];
    if (tripIdChanges && tripIdChanges.previousValue && tripIdChanges.currentValue && tripIdChanges.previousValue !== tripIdChanges.currentValue) {
      if (tripIdChanges.previousValue.name !== tripIdChanges.currentValue.name) {
        this.reactiveForm.patchValue({name: tripIdChanges.currentValue.name});
      }
      if (tripIdChanges.previousValue.start_at !== tripIdChanges.currentValue.start_at) {
        const startAt = new Date(tripIdChanges.currentValue.start_at);
        this.minDate = new NgbDate(startAt.getFullYear(), startAt.getMonth() + 1, startAt.getDate());
        this.reactiveForm.patchValue({fromDate: this.minDate});
      }
      if (tripIdChanges.previousValue.end_at !== tripIdChanges.currentValue.end_at) {
        const endAt = new Date(tripIdChanges.currentValue.end_at);
        this.maxDate = new NgbDate(endAt.getFullYear(), endAt.getMonth() + 1, endAt.getDate());
        this.reactiveForm.patchValue({toDate: this.maxDate});
      }

      if (tripIdChanges.previousValue.places.length !== tripIdChanges.currentValue.places.length) {
        this.nextSlideExists = !!this.trip.places[this.slideIndex + 1]
      }
    }
  }

  ngAfterViewInit() {
    // //const datePickerRef = this.datePicker.nativeElement;
    // if (this.trip) {
    //   this.datePicker.nativeElement.navigateTo({year: this.reactiveForm.value.fromDate.year, month: this.reactiveForm.value.fromDate.month});
    // }
  }

  ngOnDestroy(): void {
  }

  tagClicked(type: string, title: string, tag?: Tag, showExpense: boolean = true) {
    let tagData: offcanvas = {};
    tagData.type = type;
    tagData.tag = tag ? this.reactiveForm.get(type).value?.find(s => s.id === tag.id) : null;
    tagData.people = this.trip.people.map(s => { 
      return { text: s.name, value: s.name } as DropdownOption;
    })
    this.offCanvasData = tagData;
    this.offcanvastagComponent.showExpense = showExpense;
    this.offcanvastagComponent.title = tag ? `Edit ${title}` : `Add ${title}`;
    this.offcanvastagComponent.placeholderValue = this.placeholders[type];
    this.offcanvastagComponent.showOffCanvas();
  }

  saveTagData(changes: offcanvas) {
    if (changes.isDelete) {
      const updatedItems = this.reactiveForm.get(changes.type).value.filter(s => s.id !== changes.tag.id);
      this.reactiveForm.patchValue({
        [changes.type]: updatedItems
      });
    } else {
      if (!changes.tag.id) { //Add
        const updatedTag = [...this.reactiveForm.get(changes.type).value, { id: nanoid(6), name: changes.tag.name, comments: changes.tag.comments ?? null, expense: changes.tag.expense ?? null }]
        this.reactiveForm.patchValue({[changes.type]: updatedTag});
      } else { //Edit
        const indexTag = this.reactiveForm.get(changes.type).value.findIndex(s => s.id === changes.tag.id);
        const changedTag = {...this.reactiveForm.get(changes.type).value[indexTag], id: changes.tag.id, name: changes.tag.name, comments: changes.tag.comments ?? null, expense: changes.tag.expense ?? null }
        const updatedTag = [...this.reactiveForm.get(changes.type).value.slice(0, indexTag), changedTag, ...this.reactiveForm.get(changes.type).value.slice(indexTag + 1)]
        this.reactiveForm.patchValue({[changes.type]: updatedTag});
      }
    }
    this.saveTrip();
  }

  onNext() {
    this.onNavigate.emit(true);
  }

  onPrevious() {
    this.onNavigate.emit(false);
  }

  saveTrip() {
    if (this.reactiveForm.valid) {
      const { name, fromDate, toDate, transports, stays, activities, food, shopping, notes } = this.reactiveForm.value;

      let place: Place = {
        name: name,
        start_at: fromDate ? moment.utc(`${fromDate.year}-${fromDate.month}-${fromDate.day}`, 'YYYY-M-D').toISOString() : null,
        end_at: toDate ? moment.utc(`${toDate.year}-${toDate.month}-${toDate.day}`, 'YYYY-M-D').toISOString() : null,
        transports: transports ?? null,
        stays: stays ?? null,
        activities: activities ?? null,
        food: food ?? null,
        shopping: shopping ?? null,
        notes: notes ?? null
      }

      this.store.dispatch(tripActions.updatePlace({place : { ...place}}));
      this.reactiveForm.markAsPristine();
      this.offcanvastagComponent.closeOffCanvas();
      this.setFormState();
    }
  }

  cancelChanges() {
    this.setupReactiveForm(true);
    this.setFormState()
  }

  // Datepicker
  hoveredDate: NgbDate | null = null;
  current = new Date();
  minDate = {
    year: this.current.getFullYear(),
    month: this.current.getMonth() + 1,
    day: this.current.getDate()
  };
  maxDate = this.minDate;

  isDisabled = (date: NgbDateStruct) => {
    const jsDate = new Date(date.year, date.month - 1, date.day);
    return jsDate < new Date(this.minDate.year, this.minDate.month - 1, this.minDate.day) ||
      jsDate > new Date(this.maxDate.year, this.maxDate.month - 1, this.maxDate.day);
  };

  // Date picker
  onDateSelection(date: NgbDate) {
		if (!this.reactiveForm.value.fromDate && !this.reactiveForm.value.toDate) {
			this.reactiveForm.patchValue({fromDate: date});
      this.reactiveForm.get('fromDate').markAsDirty();
		} else if (this.reactiveForm.value.fromDate && !this.reactiveForm.value.toDate 
      && (date.equals(this.reactiveForm.value.fromDate) || date.after(this.reactiveForm.value.fromDate))) {
			this.reactiveForm.patchValue({toDate: date});
      this.reactiveForm.get('toDate').markAsDirty();
		} else {
			this.reactiveForm.patchValue({toDate: null});
			this.reactiveForm.patchValue({fromDate: date});
      this.reactiveForm.get('fromDate').markAsDirty();
      this.reactiveForm.get('toDate').markAsDirty();
		}
    this.setFormState();
	}

  isHovered(date: NgbDate) {
		return (
			this.reactiveForm.value.fromDate && !this.reactiveForm.value.toDate && this.hoveredDate && date.after(this.reactiveForm.value.fromDate) && date.before(this.hoveredDate)
		);
	}

  isInside(date: NgbDate) {
		return this.reactiveForm.value.toDate && date.after(this.reactiveForm.value.fromDate) && date.before(this.reactiveForm.value.toDate);
	}

	isRange(date: NgbDate) {
		return (
			date.equals(this.reactiveForm.value.fromDate) ||
			(this.reactiveForm.value.toDate && date.equals(this.reactiveForm.value.toDate)) ||
			this.isInside(date) ||
			this.isHovered(date)
		);
	}
}
