import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection } from '@angular/fire/compat/firestore';
import { AuthService } from './auth.service';
import { EventTypes } from '../models/event-types.enum';
import { Log } from '../models/log.model';

@Injectable({
  providedIn: 'root'
})
export class AppService {
  firestoreCollectionError: AngularFirestoreCollection;
  firestoreCollectionFeedback: AngularFirestoreCollection;

  constructor(private firestore: AngularFirestore, private authService: AuthService) { 
    this.firestoreCollectionError = firestore.collection<Log>('errors');
    this.firestoreCollectionFeedback = firestore.collection<Log>('feedbacks');
  }

  addError(message: string) {
    let errorObj: Log = {
      type: EventTypes.Error,
      message,
      received_at: new Date().toISOString(),
      userId: this.authService.user.uid
    }
    return this.firestoreCollectionError.add(errorObj);
  }

  addFeedback(message: string) {
    let errorObj: Log = {
      type: EventTypes.Feedback,
      message,
      received_at: new Date().toISOString(),
      userId: this.authService.user.uid
    }
    return this.firestoreCollectionFeedback.add(errorObj);
  }
}